/*=========== SPECIAL VERSION =========== */
.vspec--hide-page {
	display: none !important; }

body.#{$spec-mod} {
	//+spec-font-size--normal
	font-family: $spec-font-style--sans-serif;
	font-weight: normal;
	font-style: normal;
	font-size: $spec-font-size--medium;
	line-height: $spec-line-height--small;
	color: $spec-text-color;
	background: $spec-bg-color url(../img/empty.png) no-repeat fixed; }

/* special version settings */
/* ============================================================ */

body.#{$spec-mod} {
	// >>> Font Style <<<
	// Специальная версия: Шрифт без засечек (sans-serif)
	// ================================================================
	.#{$spec-mod--sans-serif} & {
		font-family: $spec-font-style--sans-serif; }

	// Специальная версия: Шрифт с засечками (serif)
	// ================================================================
	.#{$spec-mod--serif} & {
		font-family: $spec-font-style--serif; }

	// Специальная версия: Моноширинный шрифт (monospace)
	// ================================================================
	.#{$spec-mod--monospace} & {
		font-family: $spec-font-style--monospace; }

	// >>> Font Size <<<
	// Специальная версия: Стандартный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-normal} & {
		font-size: $spec-font-size--medium; }

	// Специальная версия: Уменьшенный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-small} & {
		font-size: $spec-font-size--small; }

	// Специальная версия: Увеличенный базовый шрифт
	// ================================================================
	.#{$spec-mod--font-large} & {
		font-size: $spec-font-size--large; }

	// >>> Line Height <<<
	// Специальная версия: Междустрочный интервал - Одинарный
	// ================================================================
	.#{$spec-mod--line-height-small} & {
		line-height: $spec-line-height--small; }

	// Специальная версия: Междустрочный интервал - Полуторный (нормальный)
	// ================================================================
	.#{$spec-mod--line-height-medium} & {
		line-height: $spec-line-height--medium; }

	// Специальная версия: Междустрочный интервал - Двойной
	// ================================================================
	.#{$spec-mod--line-height-large} & {
		line-height: $spec-line-height--large; }

	// >>> Letter Spacing <<<
	// Специальная версия: Средний межбуквенный интервал
	// ================================================================
	.#{$spec-mod--spacing-medium} & {
		letter-spacing: $spec-letter-spacing--medium;
		word-spacing: $spec-letter-spacing--medium; }

	// Специальная версия: Бальшой межбуквенный интервал
	// ================================================================
	.#{$spec-mod--spacing-large} & {
		letter-spacing: $spec-letter-spacing--large;
		word-spacing: $spec-letter-spacing--large; }

	// >>> Color scheme <<<
	// Специальная версия: Цветовая схема - Белым по черному
	// ================================================================
	.#{$spec-mod--scheme-wb} & {
		color: $spec-text-color--scheme-wb;
		background-color: $spec-bg-color--scheme-wb; }

	// Специальная версия: Цветовая схема - Синим по голубому
	// ================================================================
	.#{$spec-mod--scheme-blue} & {
		color: $spec-text-color--scheme-blue;
		background-color: $spec-bg-color--scheme-blue; }

	// Специальная версия: Цветовая схема - Коричневым по бежевому
	// ================================================================
	.#{$spec-mod--scheme-brown} & {
		color: $spec-text-color--scheme-brown;
		background-color: $spec-bg-color--scheme-brown; }

	// Специальная версия: Цветовая схема - Зеленым по темно-коричневому
	// ================================================================
	.#{$spec-mod--scheme-green} & {
		color: $spec-text-color--scheme-green;
		background-color: $spec-bg-color--scheme-green; } }

/* special version reset */
/* ============================================================ */

// reset all styles
// ------------------------------------------------------------
@import "spec/reset-full";

// reset font
// ------------------------------------------------------------
@import "spec/fonts-reset";

// reset transition
// ------------------------------------------------------------
@import "spec/transition";

// hide elements
// ------------------------------------------------------------
@import "spec/hide";

// reset svg icon color
// ------------------------------------------------------------
@import "spec/svg";

// special version background color
// ------------------------------------------------------------
@import "spec/bg";

// special reset padding
// ---------------------------------------------------------------------------
@import "spec/padding";

// special vertical space
// ---------------------------------------------------------------------------
@import "spec/spaces";

// reset images
// ------------------------------------------------------------
@import "spec/img";

// reset slider
// ------------------------------------------------------------
@import "spec/slider";

// layout spaces
// ------------------------------------------------------------
@import "spec/layout";

/* special version buttons */
/* ============================================================ */

@import "spec/btn";

/* special version typography */
/* ============================================================ */

@import "spec/typography";

/* special version components */
/* ============================================================ */

/* to top */
.#{$spec-mod} {
	.btn-to-top {
		opacity: 0.5;

		i {
			&::before, &::after {
				background-color: currentColor; } }

		.no-touchevents &:hover {
			opacity: 0.9; } } }

/* to top end */

.#{$spec-mod} {
	.full-page {
		h1, h2, h3, h4, h5, h6 {
			max-width: none; } }

	// sidebar
	.sidebar {
		width: auto !important; }

	// header
	.header-layout {
		text-align: center; }

	.logo {
		display: inline-block;
		vertical-align: top;
		width: auto;
		height: auto;
		background: none;
		border: none;
		z-index: auto;
		box-sizing: border-box;
		margin-bottom: 20px;

		a {
			height: auto;
			padding: 0;
			display: block;
			display: flex;
			justify-content: flex-start;
			align-items: center;
			text-align: left; }

		img, .logo__text {
			display: inline-block;
			margin: 0; }

		img {
			margin-right: 1em; }

		.logo__text {
			left: auto;
			top: auto;
			opacity: 1; } }

	// nav
	.nav-list {
		max-height: none !important;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		flex-wrap: wrap;

		li {
			display: inline-block;
			margin: 0.3em 0.6em 0.3em 0; }

		a {
			&::before, &::after {
				display: none; } } }

	.main {
		$space: $spec-bottom-space-item;
		padding-top: $space;
		padding-bottom: $space;

		//+media('<desk-ml')
		//space: 2.5em
		//adding-top: $space
		//adding-bottom: $space

		//+media('<tab-sm')
		//space: 2em
		//adding-top: $space
 }		//adding-bottom: $space

	.content {
		background: none; }

	.wrapper {
		min-height: 0;
		height: auto;
		overflow-y: visible;
		overflow-x: hidden;
		z-index: 6; }

	.footer {
		border-top: $spec-border-width solid currentColor;
		padding: 20px 0; }

	.btn-menu {
		@include size($main-nav-height--spec);
		display: inline-block;
		display: flex;
		display: none;
		justify-content: center;
		line-height: 24px;
		align-items: center;
		//color: $spec-text-color
		color: currentColor;
		transition: none;
		box-sizing: border-box;
		border: 0 solid currentColor;
		//background-color: $spec-bg-color
		background: none;
		text-decoration: underline;
		-webkit-tap-highlight-color: transparent;

		//.#{$spec-mod--scheme-wb} &
		//ackground-color: $spec-bg-color--scheme-wb
		//olor: $spec-text-color--scheme-wb

		//.#{$spec-mod--scheme-blue} &
		//ackground-color: $spec-bg-color--scheme-blue
		//olor: $spec-text-color--scheme-blue

		//.#{$spec-mod--scheme-brown} &
		//ackground-color: $spec-bg-color--scheme-brown
		//olor: $spec-text-color--scheme-brown

		//.#{$spec-mod--scheme-green} &
		//ackground-color: $spec-bg-color--scheme-green
		//olor: $spec-text-color--scheme-green

		@include media('<tab-lg') {
			display: inline-block;
			display: flex; }

		&::before {
			display: none; }

		& > span {
			display: inline;
			vertical-align: middle;
			font-size: 18px;
			line-height: 24px;
			font-weight: bold;
			letter-spacing: $spec-letter-spacing--normal;
			text-transform: uppercase;

			@include media('<mob-md') {
				font-size: 14px; } }

		& > i {
			display: none;

			&, &::before, &::after {
				transition: none;
				background: currentColor; } } }

	// icon
	[class*="icon-"] {
		font-style: normal; }

	// breadcrumbs
	.breadcrumbs {
		@include spec-font-size--note; }

	.breadcrumbs-list {
		li, a, span {
			display: inline;
			vertical-align: baseline;
			line-height: inherit;
			white-space: normal; }

		li {
			padding: 0;
			margin: 0 0.4em 0 0;

			&::before {
				color: inherit; }

			&, &:first-child {
				&::before {
					position: relative;
					left: auto;
					top: auto;
					display: inline;
					background: none;
					width: auto;
					height: auto;
					border: none;
					margin: 0 0.4em 0 0; } }

			&:last-child {
				& > span {
					display: inline;
					white-space: normal;
					max-width: none;
					overflow: visible;
					text-overflow: unset; } } }

		a {
			&:hover, &:focus {
				color: inherit; } } }

	// user form
	.user-form {
		background: none;

		.input-wrap {
			&::before, &:after {
				display: none; } }

		.label-holder {
			float: none;
			width: auto;
			padding: 0;
			margin-bottom: 3px;

			&::before {
				display: none; }

			label, .label {
				max-width: none;
				display: inline;
				vertical-align: initial; } }

		.input-holder {
			margin-left: 0; }

		.form-footer {
			margin-left: 0;
			padding-left: 0; }

		.form-buttons {
			float: none;
			margin-right: 0;
			margin-bottom: $spec-bottom-space-element;

			&:last-child {
				margin-bottom: 0; }

			.btn-submit {
				width: auto; } }

		.form-note {
			height: auto;
			margin-bottom: $spec-bottom-space-element;

			&:last-child {
				margin-bottom: 0; }

			&::before {
				display: none; } }

		.form-note__item {
			width: auto;
			display: block; } }

	// jFiler
	.jFiler {
		color: inherit; }

	.jFiler-input-dragDrop {
		@include spec-reset-bg;
		border: $spec-border-width dashed currentColor;
		color: currentColor; }

	.jFiler-input-icon {
		display: none !important; }

	.jFiler-input-text {
		strong {
			@include spec-font-reset;
			text-align: center; } }

	// files
	.file {
		display: inline-block;
		vertical-align: top;
		height: auto !important;

		[class*="depict-files-"] {
			float: left;
			position: relative;
			left: auto;
			top: auto;
			margin: 0.5em 20px 0 0; }

		.file__content {
			overflow: hidden; }

		&:hover {
			[class*="depict-files-"],
			[class*=" depict-files-"] {
				&::before {
					opacity: 1; }

				&::after {
					display: none; } }

			.file__title,
			.file__info {
				color: inherit; } } }

	// movie
	.movie__item {
		h3 {
			height: auto !important; } }

	.movie__text {
		height: auto !important; }

	.movie__video-img {
		position: relative;
		display: inline-block;
		vertical-align: top;

		i {
			display: none; }

		&::after {
			display: none; }

		& > span {
			display: block; } }

	// call section
	.call-content h2 {
		max-width: none; }

	.call-list {
		display: flex;
		flex-wrap: wrap; }

	.call-item {
		display: inline-block;
		display: flex;
		padding-right: 10px; }

	.call-item__row {
		display: flex;
		justify-content: flex-start;
		align-items: flex-start; }

	.call-item__img, .call-item__text {
		display: inline-block;
		margin-right: 10px; }

	// social network
	.social-networks {
		li {
			display: inline-block;
			vertical-align: top;
			margin-right: 1em;
			margin-bottom: 1em; }

		a {
			float: none;
			text-indent: 0;
			overflow: visible;
			width: auto;
			height: auto;
			border: none;
			border-radius: 0;
			box-sizing: border-box;
			transition: none;
			font-weight: bold; } }

	// developer
	.developer {
		display: inline;
		height: auto;
		width: auto;
		position: relative;
		bottom: auto;
		left: auto;
		text-indent: 0;
		overflow: visible;
		border: none;
		background: none;
		box-sizing: border-box; } }

