@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {
	.header,
	.main-layout {
		box-sizing: border-box;
		max-width: calc(#{$spec-text-page} + #{nth($spec-page-space, 2)} * 2);
		@include margin-horizontal(auto);
		@include padding-horizontal(nth($spec-page-space, 2));

		@include media('<tab-lg') {
			max-width: none;
			@include padding-horizontal(nth($spec-page-space--md, 2)); }

		@include media('<mob-md') {
			@include padding-horizontal(nth($spec-page-space--sm, 2)); } }

	.main {
		margin: 0 auto;
		max-width: $spec-text-layout;

		@include media('<tab-lg') {
			max-width: $spec-text-layout--sm; }

		@include media('<tab-sm') {
			max-width: none; } } }
