@import "../../libs/include-media/dist/include-media";
@import "../utils/special-version-variables";
@import "../utils/mixins";

.#{$spec-mod} {
	// hide element
	// ---------------------------------------------------------------------------
	.sidebar,
	.sidebar-overlay,
	.sidebar-large, // копия сайдбарара, добавленная скриптом
	.sidebar .logo,
	.btn-sidebar,
	.header .search-form,
	.btn-site-map,
	.site-map,
	.about-desc,
	.about-info__img,
	.btn-default .depict-download,
	.call-img,
	.footer-container,
	.social-networks a > i,
	.spec-hide,
	.spec-hide {
		display: none !important; }


	// hide ::before
	// ---------------------------------------------------------------------------
	.about-info__col,
	.about-info,
	.about-info__list,
	.file,
	.social-networks a,
	.call-item,
	.spec-hide-before,
	.spec-hide-before {
		&::before {
			display: none; } }


	// hide ::after
	// ---------------------------------------------------------------------------
	.about-info,
	.about-info__list,
	.file,
	.social-networks,
	.spec-hide-after,
	.spec-hide-after {
		&::after {
			display: none; } } }
